@import url(https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
.home-panel>div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    grid-gap: 50px;
    gap: 50px;
}

.home-panel__greeting>div {
    display: flex;
    flex-direction: row;
}

.home-panel__actions>div {
    display: flex;
    flex-direction: row;
    grid-gap: 30px;
    gap: 30px;
}

.home-panel__link {
    padding: 10px 20px;
    background-color: #e5ebf1;
    border-radius: 8px;
    color: #55677D;
    font-weight: 500;
}
.login__spinner-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main__container {
  width: 100%;
  display: flex;
  height: 100vh;
  justify-content: space-between;
  align-items: center;
  padding: 0 16px;
  box-sizing: border-box;
}

.main__logo {
  font-family: 'Playfair Display', serif;
  font-size: 22px;
  border-color: var(--black);
  border-style: solid;
  border-width: 1px 1px 1px 1px;
  padding: 0 2px;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}

.main__form {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.form__content {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.form__later {
  /* margin: 0 16px; */
  box-sizing: border-box;
  padding-left: 0;
}
